.container {
    margin: 0 auto;
    max-width: 1200px;
}
.conteudo {
    display: flex;
    height: 100%;
}
.geral {
    height: calc(100vh - var(--tamanho-cabecalho) - var(--tamanho-rodape));
}
@media screen and (max-width: 400px) {
    .conteudo {
        flex-direction: column;
    }
}